<script>
    export let vital;
    export let maxVital;
    export let color;

    $: percentVital = maxVital > 0 ? Math.floor((100 * vital) / maxVital) : 0;
</script>

<div class="progressbar" >
    <div class="bar {color}" style="width: {percentVital}%"></div>
</div>

<style>
    .progressbar {
        position: relative;
        margin: 5px 0 0 0;
        width: 100%;
        height: 5px;
        background: #e1e1e1;
        border-radius: 2px;
        overflow: hidden;
        box-shadow: 0px 0px 12px rgba(123, 104, 238, 0.7);
    }
    .progressbar .bar {
        height: 100%;
        background: #212121;
    }
    .bar.blue {
        background: #00a8c5;
    }
    .bar.red {
        background: #be5869;
    }
</style>
