<script>
    import { createEventDispatcher } from 'svelte';

    let dispatch = createEventDispatcher();

    export let name; // Used to get filepath to image
    export let bSelected;
    export let size = 80;
    const imgFolder = './images/';
    $: imgPath = imgFolder + name + '.png';

    const handleSelect = e => {
        dispatch('select', e);
    };
</script>

<div class="wrapper" class:bSelected>
    <div class="icon" class:bSelected on:click="{e => handleSelect(e)}" style="width: {size}px; height: {size}px">
        <img src="{imgPath}" alt="Missing Player Icon!" />
    </div>
</div>

<style>
    img {
        height: 100%;
        width: 100%;
        object-fit: contain;
    }
    .wrapper {
        padding: 0px;
    }
    .wrapper.bSelected {
        padding: 0;
    }
    .icon {
        border-radius: 50%;
        border: 2px solid;
        border-color: black;
        box-shadow: 0px 0px 12px rgba(123, 104, 238, 0.7);
        overflow: hidden;
        margin: 0 0 0 0.5em;
    }
    .icon:hover {
        box-shadow: 0px 0px 12px rgba(87, 4, 104, 0.904);
    }
    .icon.bSelected {
        margin: 10;
    }
</style>
