<script>
    import { createEventDispatcher } from 'svelte';
    import { tweened } from 'svelte/motion';
    import { scale } from 'svelte/transition';
    import { quintOut } from 'svelte/easing';
    import PlayerIcon from './PlayerIcon.svelte';
    import Vitalbar from './Vitalbar.svelte';

    let dispatch = createEventDispatcher();

    export let index;
    export let name;
    export let hitpoints;
    export let maxHitpoints;
    export let mana;
    export let maxMana;
    export let bSelected;

    const store = tweened(0, { duration: 200 });
    $: store.set(bSelected ? 100 : 75);

    const handleSelect = e => {
        dispatch('select', index);
    };
</script>

<div class="player" class:selected="{bSelected}">
    <PlayerIcon {name} {bSelected} on:select="{e => handleSelect(e)}" size="{$store}" />
    <div class="info">
        {#if bSelected}
            <div class="name">
                <span>{name}</span>
            </div>
        {/if}
        <div class="hitpoints" class:selected="{bSelected}">
            {#if bSelected}
                <span>{hitpoints}/{maxHitpoints}</span>
            {/if}

            <Vitalbar vital="{hitpoints}" maxVital="{maxHitpoints}" color="red" />
        </div>
        <div class="mana" class:selected="{bSelected}">
            {#if bSelected}
                <span>{mana}/{maxMana}</span>
            {/if}
            <Vitalbar vital="{mana}" maxVital="{maxMana}" color="blue" />
        </div>
    </div>
</div>

<style>
    .player {
        color: #ffffff;
        width: 100%;
        margin: 0 0 0.5em 0;
        display: block;
        justify-content: space-between;
        box-sizing: border-box;
    }
    .player.selected {
        display: flex;
    }
    .info {
        margin: 0.6em;
        height: 100%;
        flex: 1;
        user-select: none;
        font-family: 'Comic Sans MS', cursive;
        font-size: 1em;
    }

    .hitpoints {
        width: 75px;
    }
    .hitpoints.selected {
        width: 100px;
    }
    .mana {
        width: 75px;
    }
    .mana.selected {
        width: 100px;
    }
</style>
