<script>
    import PlayerComponent from './PlayerComponent.svelte';
    // Name will also be used to locate icon

    let selectedIndex = -1;
    let bShiftKeyDown = false;

    let heroes = [
        // {
        //     name: 'Cyphina',
        //     hitpoints: 100,
        //     maxHitpoints: 100,
        //     mana: 100,
        //     maxMana: 100,
        //     bSelected: false,
        // },
        // {
        //     name: 'Kitty',
        //     hitpoints: 100,
        //     maxHitpoints: 100,
        //     mana: 100,
        //     maxMana: 100,
        //     bSelected: false,
        // },
        // {
        //     name: 'Emelyene',
        //     hitpoints: 100,
        //     maxHitpoints: 100,
        //     mana: 100,
        //     maxMana: 100,
        //     bSelected: false,
        // },
        // {
        //     name: 'Sylphia',
        //     hitpoints: 100,
        //     maxHitpoints: 100,
        //     mana: 100,
        //     maxMana: 100,
        //     bSelected: false,
        // },
    ];

    const handleSelect = e => {
        let newSelectedIndex = e.detail;
        if (!bShiftKeyDown) {
            for (let i = 0; i < heroes.length; ++i) {
                heroes[i].bSelected = false;
            }
        }
        heroes[newSelectedIndex].bSelected = bShiftKeyDown ? !heroes[newSelectedIndex].bSelected : true;
        blu_event('selectHero', '{"value":' + newSelectedIndex + '}');
        heroes = heroes;
    };

    const handleKeyDown = e => {
        if (e.key == 'Shift') bShiftKeyDown = true;
    };

    const handleKeyUp = e => {
        if (e.key == 'Shift') bShiftKeyDown = false;
    };

    // Update all the heroes when replacing a party
    window.updatePartyInfo = function(newData) {
        heroes = newData;
    };

    // Update a hero by passing an object with an index (heroIndex) and new values (updatedHeroData)
    window.updateHeroInfo = function(newData) {
        const key = 'heroIndex' in newData ? newData.heroIndex : newData.name; // If there is no property heroIndex, we'll search by name
        console.log(key);
        if (isNaN(key)) {
            let index = heroes.findIndex(hero => hero.name === key);
            heroes[index] = { ...heroes[index], ...newData };
        } else {
            heroes[key] = { ...heroes[key], ...newData };
        }
    };

    window.updateDeselectAllHeroes = function(newData) {
        heroes.forEach(hero => (hero.bSelected = false));
        heroes = heroes;
    };

    window.updateHeroToggleDeselect = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].bSelected = false;
        }
    };

    window.updateSingleHeroSelect = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes.forEach(hero => (hero.bSelected = false));
            heroes[index].bSelected = true;
        }
    };

    window.updateSingleHeroToggleSelect = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].bSelected = true;
        }
    };

    window.updateHeroHealthInfo = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].hitpoints = newData.hitpoints;
        }
    };

    window.updateHeroManaInfo = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].mana = newData.mana;
        }
    };

    window.updateHeroMaxHealthInfo = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].maxHitpoints = newData.maxHitpoints;
        }
    };

    window.updateHeroMaxManaInfo = function(newData) {
        if ('name' in newData) {
            let index = heroes.findIndex(hero => hero.name === newData.name);
            heroes[index].maxMana = newData.maxMana;
        }
    };
</script>

<svelte:window on:keydown="{handleKeyDown}" on:keyup="{handleKeyUp}" />

<main>
    <div class="heroContainer">
        {#each heroes as hero, index}
            <PlayerComponent {...hero} {index} on:select="{e => handleSelect(e)}" />
        {/each}
    </div>
</main>

<style>
    main {
        box-sizing: border-box;
    }

    .heroContainer {
        height: 100%;
        width: 100%;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
</style>
